html{font-size:16px;font-family:"Arial","LiHei Pro Medium","Microsoft YaHei","黑体","宋体",sans-serif}body{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;margin:0}body .container{padding:2rem 20rem}@media (max-width: 1140px){body .container{max-width:calc(1140px - 40rem);padding:2rem 0;margin:0 auto}}body #app{color:#555;width:100%}body a{color:#454545}body a:hover{color:#0dc7e3;text-decoration:none}body p{text-align:justify}.fade-transition{-webkit-transition:all .3s ease;transition:all .3s ease;opacity:1}.fade-enter,.fade-leave{opacity:0}



